.border{
  background-color: rgb(51, 244, 244);
  position: relative;
}
.wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-auto-rows: 200px;
  grid-gap:5px 5px;
  
}
.wrapper > div {
  border: 2px solid #0431f6;
  border-radius: 5px;
  background-color: #caeffa;
  padding: 1em;
  color: #000000;
}
.heart {
  width:40px;
  position:absolute;
  left:10%;
  top: 70%;
  z-index: 999;
}
.iineP{
  position:absolute;
  left: 30%;
  top:70%;
  
}
.dcchan{
  width:100%;
  z-index:-100;
  position:absolute;
}

.textbox-001-label,
.textbox-001 {
    color: #333;
}

.textbox-001-label {
    display: block;
    margin-bottom: 5px;
    font-size: .9em;
}

.textbox-001 {
    width: 100%;
    padding: 8px 10px;
    border: 1px solid #969da3;
    border-radius: 25px;
    font-size: 1em;
    line-height: 1.5;
}

.textbox-001::placeholder {
    color: #999;
}
a.btn_01 {
	display: block;
	text-align: center;
	vertical-align: middle;
	text-decoration: none;
	width: 120px;
	margin: auto;
	padding: 1rem 4rem;
	font-weight: bold;
	border: 2px solid #27acd9;
	color: #27acd9;
	transition: 0.5s;
}
a.btn_01:hover {
	color: #fff;
	background: #27acd9;
}